/**
 * FooterBodyFactory - Creates the actual footer content based on footer kind
 * This is the DRY equivalent of the individual footer components
 */

import React from 'react';
import styles from './Footer.module.scss';
import type {
	FooterKind,
	FooterLink,
} from './configurations';

export interface FooterBodyFactoryProps {
	kind: FooterKind;
	leftContent?: React.ReactNode;
	rightContent?: React.ReactNode;
	centerContent?: React.ReactNode;
	copyright?: string;
	links?: FooterLink[];
	showDivider?: boolean;
	[key: string]: any;
}

/**
 * Simple footer content component
 */
const SimpleFooterBody: React.FC<any> = (props) => {
	const { copyright } = props;
	const defaultCopyright =
		copyright ||
		`© ${new Date().getFullYear()} UI Kit Modern`;

	return (
		<>
			<div className={styles.footerLeft}>
				{defaultCopyright}
			</div>
			<div className={styles.footerRight}></div>
		</>
	);
};

/**
 * Standard footer with navigation links
 */
const StandardFooterBody: React.FC<any> = (props) => {
	const {
		copyright,
		links = [],
		showDivider = true,
	} = props;
	const defaultCopyright =
		copyright ||
		`© ${new Date().getFullYear()} UI Kit Modern`;

	return (
		<>
			<div className={styles.footerLeft}>
				{defaultCopyright}
			</div>
			<div className={styles.footerRight}>
				{links.length > 0 && (
					<nav className={styles.footerNav}>
						{links.map(
							(link: FooterLink, index: number) => (
								<React.Fragment
									key={link.href || link.label || index}
								>
									{link.onClick ?
										<button
											onClick={link.onClick}
											className={styles.footerLink}
											type='button'
										>
											{link.label}
										</button>
									: link.href ?
										<a
											href={link.href}
											className={styles.footerLink}
											target={
												link.href.startsWith('http') ?
													'_blank'
												:	undefined
											}
											rel={
												link.href.startsWith('http') ?
													'noopener noreferrer'
												:	undefined
											}
										>
											{link.label}
										</a>
									:	<span className={styles.footerLink}>
											{link.label}
										</span>
									}
									{showDivider &&
										index < links.length - 1 && (
											<span
												className={styles.footerDivider}
											>
												•
											</span>
										)}
								</React.Fragment>
							)
						)}
					</nav>
				)}
			</div>
		</>
	);
};

